import { Component, ViewChild, ElementRef, OnInit, NgZone } from '@angular/core';
import { ElectronService } from 'ngx-electron';
import { faCheck, faTimes } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  @ViewChild('icon', { static: false }) icon: ElementRef;
  @ViewChild('icoFilePath', { static: false }) icoFilePath: ElementRef;
  @ViewChild('description', { static: false }) description: ElementRef;
  @ViewChild('copyright', { static: false }) copyright: ElementRef;
  @ViewChild('filevers', { static: false }) filevers: ElementRef;
  @ViewChild('product', { static: false }) product: ElementRef;
  @ViewChild('prodvers', { static: false }) prodvers: ElementRef;
  @ViewChild('orgname', { static: false }) orgname: ElementRef;
  @ViewChild('intname', { static: false }) intname: ElementRef;

  public accepted = false;

  public loading = false;

  public success = false;
  public error = false;

  public exe = false;
  public exePath = '';

  public iconPath = '';
  public fileDescription = '';
  public legalCopyright = '';
  public fileVersion = '';
  public productName = '';
  public productVersion = '';
  public originalFilename = '';
  public internalName = '';

  public faCheck = faCheck;
  public faTimes = faTimes;

  constructor(private electronSrv: ElectronService, private zone: NgZone) { }

  ngOnInit() {
    this.accepted = localStorage.getItem('accept') ? true : false;
    if (this.electronSrv.isElectronApp) {
      this.electronSrv.ipcRenderer.on('proc_success', (event, exeDetails) => {
        this.zone.run(() => {
          this.loading = false;
          this.exe = true;

          this.fileDescription = typeof exeDetails.FileDescription === 'undefined' ? '' : exeDetails.FileDescription;
          this.legalCopyright = typeof exeDetails.LegalCopyright === 'undefined' ? '' : exeDetails.LegalCopyright;
          this.fileVersion = typeof exeDetails.FileVersion === 'undefined' ? '' : exeDetails.FileVersion;
          this.productName = typeof exeDetails.ProductName === 'undefined' ? '' : exeDetails.ProductName;
          this.productVersion = typeof exeDetails.ProductVersion === 'undefined' ? '' : exeDetails.ProductVersion;
          this.originalFilename = typeof exeDetails.OriginalFilename === 'undefined' ? '' : exeDetails.OriginalFilename;
          this.internalName = typeof exeDetails.InternalName === 'undefined' ? '' : exeDetails.InternalName;
        });
      });

      this.electronSrv.ipcRenderer.on('mod_success', () => {
        this.zone.run(() => {
          this.success = true;
          this.loading = false;
          this.exe = false;
          this.exePath = '';
        });
      });

      this.electronSrv.ipcRenderer.on('mod_error', () => {
        this.zone.run(() => {
          this.error = true;
          this.loading = false;
          this.exe = false;
          this.exePath = '';
        });
      });
    }
  }

  accept() {
    localStorage.setItem('accept', 'yes');
    this.accepted = true;
  }

  chooseExe() {
    if (this.electronSrv.isElectronApp) {
      this.success = false;
      this.error = false;
      this.loading = false;
      this.exe = false;
      const remote = this.electronSrv.remote;
      const win = remote.getCurrentWindow();
      const dialog = remote.dialog;
      dialog.showOpenDialog(win, {
        title: 'Open executable',
        filters: [
          { name: 'Executable', extensions: ['exe'] }
        ]
      }).then(result => {
        if (result && !result.canceled) {
          this.electronSrv.ipcRenderer.send('proc_exe_details', result);
          this.exe = true;
          this.exePath = result.filePaths[0];
        } else {
          this.exe = false;
          this.exePath = '';
        }
      }).catch(err => {
        console.log(err);
      });
    }
  }

  chooseIcon() {
    if (this.electronSrv.isElectronApp) {
      const remote = this.electronSrv.remote;
      const win = remote.getCurrentWindow();
      const dialog = this.electronSrv.remote.dialog;
      dialog.showOpenDialog(win, {
        title: 'Choose icon',
        filters: [
          { name: 'Images', extensions: ['ico'] }
        ]
      }).then(result => {
        if (result && result.filePaths.length > 0) {
          this.icon.nativeElement.src = 'file:///' + result.filePaths[0];
          this.icon.nativeElement.title = result.filePaths[0];
          this.iconPath = result.filePaths[0];
        }
      }).catch(err => {
        console.log(err);
      });
    }
  }

  apply() {
    this.loading = true;
    const exeDetails = {
      ExePath: this.exePath,
      IconPath: this.icoFilePath.nativeElement.value,
      FileDescription: this.description.nativeElement.value,
      LegalCopyright: this.copyright.nativeElement.value,
      FileVersion: this.filevers.nativeElement.value,
      ProductName: this.product.nativeElement.value,
      ProductVersion: this.prodvers.nativeElement.value,
      OriginalFilename: this.orgname.nativeElement.value,
      InternalName: this.intname.nativeElement.value
    };
    this.electronSrv.ipcRenderer.send('apply_exe_details', exeDetails);
  }

  cancel() {
    this.exe = false;
    this.exePath = '';

    this.fileDescription = '';
    this.legalCopyright = '';
    this.fileVersion = '';
    this.productName = '';
    this.productVersion = '';
    this.originalFilename = '';
    this.internalName = '';
  }
}
